@font-face {
    font-family: 'Myriad Italic';
    src: local("Myriad Italic"), url("./assets/Myriad Pro Italic.ttf") format("truetype");
}

@font-face {
    font-family: "Myriad Bold";
    src: local("Myriad Bold"), url("./assets/Myriad Pro Bold.ttf") format("truetype");
}


body {
    background: #000 !important;
}

.loader {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    z-index: 5;
}

.wallet-input {
    color: #FFF;
    background: #000;
    width: 100%;
    border-width: 0px;
    border: none;
    outline: none;
    font-size: 1.3vw;
}

.wallet-input::placeholder {
    color: #FFF
}

.loading-container {
    position: absolute;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    top: 50%;
}

.connect-container {
    background: #000;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 50%;
    height: 15%;
    padding-right: 7px;
    padding-left: 7px;
}

.blocker {
    position: absolute;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    background: #000;
    z-index: 10;
    color: #fff;
    justify-content: center;
    align-items: center;

}

.catlogo-text {
    font-family: 'Myriad Bold';
    position: absolute;
    top: -3%;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    text-align: center;
}

.disclaimer {
    position: absolute;
    width: 100%;
    text-align: center;
    font-size: 1.5vw;
}

.app-container {
    font-family: 'Myriad Italic';
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #fff;
    position: absolute;
    width: 100%;
    height: 100%;
    /* overflow: hidden; */
    /* border:1px solid red; */
    /* padding-top:2%; */
    /* padding-bottom: 3%; */
    padding-left: 3%;
    padding-right: 3%;
}

.btn-main {
    align-items: center;
    background-color: #fff;
    border: 0;
    box-sizing: border-box;
    color: #000;
    cursor: pointer;
    display: inline-flex;
    justify-content: center;
    overflow: hidden;
    padding: 0px;
    padding-left: 4%;
    padding-right: 4%;
    padding-top: 0.4%;
    padding-bottom: 0.4%;
    text-align: center;
    vertical-align: middle;
    font-family: 'Myriad Bold';
    font-size: 1.2vw;
}


.btn-connect {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    font-family: 'Myriad Bold';
    padding-left: 1%;
    padding-right: 1%;
    height: 12%;
}

.btn-save {
    height: 80%;
}

.header {
    display: flex;
    flex-direction: column;
    padding: 2%;
    padding-left: 19%;
    font-size: 1vw;
    padding-right: 19%;
    /* border: 1px solid red; */

}

.heading-text {
    text-align: center;
}

.image-container {
    width: 62%;
    height: auto;
    position: relative;
    overflow: hidden;
    width: 62%;
    aspect-ratio: 3/1;
    /* border:1px solid red */
}

.edit-container {
    width: 100%;
    height: 31%;
    padding-left: 19%;
    padding-right: 19%;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    /* border: 1px solid red; */

}

.heading-title {
    font-family: 'Myriad Bold';
    font-size: 3vw;
    display: flex;
    justify-content: center;
}

.row-wrap {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.bottom-row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
}

.text-display {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 5%;
    border-radius: 100px;
    background: transparent;
    color: #fff;
    outline: none;
    padding-left: 5px;
    height: 8%;
    z-index: 2;
    padding-bottom: 5px;
    padding-right: 5px;


}

.cgf-logo {
    width: 100%;
    height: 100%;
}


.pinata-cat {
    width: 100%;
    height: 100%;
}

.flex-container {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    /* border: 1px solid red; */
    height: 60%;
    display: flex;
    flex-direction: row;
}

.row {
    /* border: 1px solid blue;    */
    height: 100%;
    width: 100%;
    flex-direction: row;
    margin-right: 0px !important;
    margin-left: 0px !important;
}

.phone-carousel {
    height: 4%;
    width: 100%;
    background: #fff;
    color: #000;
    border-radius: 100px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-family: Myriad Italic;
    font-size: 4vw;
    align-items: center;
    margin-top: 2%;
    margin-bottom: 2%;

}



@media only screen and (max-width: 700px) {
    .header {
        padding-left: 0%;
        font-size: 4vw;
        padding-right: 0%
    }

    .heading-title {
        font-size: 10vw;
    }

    .image-container {
        width: 100%;
        margin-top: 20px;
    }

    .btn-connect {
        font-size: 3.2vw;
        height: 17%;
    }

    .btn-main {
        font-size: 2.5vw;

    }

    .connect-container {
        width: 63%;
        height: 23%;
    }

    .edit-container {
        width: 100%;
        height: 40%;
        padding-left: 0%;
        padding-right: 0%;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        margin-top: 20px;

    }

    .btn-save {
        height: 30%;
        width: 23%;
        font-size: 3vw;
        position: relative;
        left: 50%;
        transform: translateX(-50%);
    }

    .bottom-row {
        flex-direction: column;
        height: 30%;
    }

    .title {
        font-size: 4vw !important;
    }

    .wallet-input {
        font-size: 3vw;
    }


}