.color-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    height: 50px;
    margin-left: 6.8%;
}

.colorpick-wrapper {
    display: flex;
    flex-direction: row;
    align-items: center
}

.title {
    font-family: 'Myriad Italic ';
    font-size: 1vw;
}

.color {
    position: relative;
    height: 50%;
    cursor: pointer;
    aspect-ratio: 1/1;
}

.color-under {
    position: absolute;
    width: 100%;
    height: 17%;
    bottom: -40%;
}

.color:not(:last-child) {
    margin-right: 4%
}

@media only screen and (max-width: 700px) {
    .colorpick-wrapper {
        flex-direction: column;
    }

    .color-wrapper {
        width: 100%;
        justify-content: center;
    }


}