.row-wrap-picker {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.dropdown-trigger {
  position: relative;
  background: #FFF;
  color: #000;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  aspect-ratio: 1/1;
  margin-left: 5px;
  cursor: pointer;
}

.dropdown-trigger-under {
  background: #FFF;
  height: 15%;
  top: bottom;
  width: 100%;
  position: absolute;
  bottom: -35%;
}

.selector {
  font-family: 'Myriad Bold';
  position: relative;
  background: #FFF;
  color: #000;
  font-size: 1vw;
  width: 12%;
  text-align: center;
  height: 100%;
}

.title {
  font-family: 'Myriad Italic';
  font-size: 1vw;
  white-space: nowrap;
}

.dropdown-content {

  flex-direction: column;
  background: #fff;
  width: 100%;
  overflow-y: scroll;
}

.dropdown-caret {
  font-size: 1.7vw;
  position: absolute;
  bottom: -30%;
  right: 3%;
}

.cat {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin-left: 2px;
  cursor: pointer;
}

.cat-check {
  cursor: pointer;
}

.cat-check:checked {}

/* CHECKBOX SCROLLBAR */
/* width */
.dropdown-content::-webkit-scrollbar {
  width: 7px;
}

/* Track */
.dropdown-content::-webkit-scrollbar-track {
  background: #f1f1f1;

}

/* Handle */
.dropdown-content::-webkit-scrollbar-thumb {
  background: #000;
}

/* CHECKBOX SCROLLBAR */



/* remove SCROLLBAR */
.remove-wrapper::-webkit-scrollbar {
  width: 10px;
}

/* Track */
.remove-wrapper::-webkit-scrollbar-track {
  background: #000;
  /* border-top-right-radius: 40%;
      border-bottom-right-radius:40% ; */
}

/* Handle */
.remove-wrapper::-webkit-scrollbar-thumb {
  background: #dd67ff;
  border-radius: 10px;
  border: 6px solid #000;
}

/* remove SCROLLBAR */

.x-icon {
  height: 60%;
  width: 1vw
}

.outer-wrapper {
  position: absolute;
  display: flex;
  z-index: 4;
  width: 100%;
  left: 0;
  margin-top: 4px;
  height: auto;
  max-height: 542%;
  overflow: hidden;

}

.remove-wrapper {
  display: flex;
  flex-direction: row;
  overflow-x: auto;
  width: 76%;
}

@media only screen and (max-width: 700px) {
  .selector {
    font-size: 3vw;
    width: 22%;
  }
}