.row-wrap-text {
    display: flex;
    flex-direction: row;
    align-items: center;

}

.text-input {
    background: #FFF;
    border: 2px solid white;
    margin-left: 10px;
    color: #000;
    outline: none;
    padding-left: 5px;
    height: 100%;
    width: 35%;
    font-size: 1vw;
    margin-left: 13.7%;

}

.title-content {
    font-family: 'Myriad Italic';
    font-size: 1vw;
    white-space: nowrap;
}

@media only screen and (max-width: 700px) {
    .title-content {
        font-size: 4vw;
    }

    .text-input {
        width: 50%;
        font-size: 3vw;
    }
}